<template>
  <div>
 
    <p>-------------------------------router-link--------------------------</p>
    <router-link to="/food" v-if="show=='1'">外卖</router-link>
    <router-link to="/move" v-else>电影</router-link>
    <p>$router.push(path)添加一条新的历史记录可以返回</p>
    <hr />
    <button @click="$router.push('/food')" v-if="show=='1'">外卖</button>
    <button @click="$router.push('/move')" v-else>电影</button>
    <p>$router.replace(path)添加一条新的历史记录去替换当前记录</p>
    <hr />
    <button @click="$router.replace('/food')" v-if="show=='1'">外卖</button>
    <button @click="$router.replace('/move')" v-else>电影</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: localStorage.getItem("isRole"),
    };
  },
  mounted() {
    console.log("首页挂载");
  },
  destroyed() {
    console.log("首页销毁");
  },
};
</script>

<style>
</style>


